<template>
  <aside class="GlobalSideBar">
    <login-basic/>
    <aside-quick-entry/>
    <aside-hot-recommend/>
    <div class="Card ad-wrap">
      <img
        src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1583556034543&di=5a179d742a5571c8c8b896925565e550&imgtype=0&src=http%3A%2F%2Ffile04.16sucai.com%2Fd%2Ffile%2F2015%2F0415%2F7e15aad3ed184ab3ec2f0abde5d90430.jpg"
        class="full-img-base"
      >
    </div>
    <div class="Card ad-wrap">
      <img
        src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1583556034543&di=5a179d742a5571c8c8b896925565e550&imgtype=0&src=http%3A%2F%2Ffile04.16sucai.com%2Fd%2Ffile%2F2015%2F0415%2F7e15aad3ed184ab3ec2f0abde5d90430.jpg"
        class="full-img-base"
      >
    </div>
    <div
      class="fixed-wrap"
      :style="getFixedStyle"
      ref="fixedBlock"
    >
      <aside-web-footer/>
    </div>
  </aside>
</template>

<script>
  import LoginBasic from '../../components/LoginBasic'
  import AsideWebFooter from './AsideWebFooter'
  import AsideHotRecommend from './AsideHotRecommend'
  import AsideQuickEntry from './AsideQuickEntry'

  export default {
    mounted() {
      this.offsetTop = this.$refs.fixedBlock.getBoundingClientRect().top;
      // console.log('---->', this.$refs.fixedBlock.getBoundingClientRect());
      window.addEventListener('scroll', this.handleScroll);
      this.$once('hook:beforeDestroy', () => {
        window.removeEventListener('scroll', this.handleScroll);
      });
      window.addEventListener("beforeunload", () => {
        window.scrollTo(0, 0);
      });
    },
    methods: {
      handleScroll() {
        // console.log('handleScroll', this.$refs.fixedBlock.getBoundingClientRect());
        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop || window.pageYOffset;
        // const offsetTop = document.querySelector('#fixedId').offsetTop
        // console.log('handleScroll', scrollTop, this.offsetTop);
        if (scrollTop > this.offsetTop) {
          this.isFixed = true;
          this.scrollTop = scrollTop
        } else {
          this.isFixed = false
        }
      },
    },
    computed: {
      // 计算属性的 getter
      getFixedStyle() {
        if (this.isFixed) {
          let top = this.scrollTop - this.offsetTop;
          return 'top: ' + top + 'px;'
        } else {
          return {}
        }
      }
    },
    data() {
      return {
        isFixed: false,
        offsetTop: 0,
        scrollTop: 0,
      }
    },
    components: {LoginBasic, AsideWebFooter, AsideHotRecommend, AsideQuickEntry},
    name: "AsideBasic",
  }
</script>

<style lang="less" scoped>
  .GlobalSideBar {
    width: 300px;
    font-size: 14px;

    .ad-wrap {
      position: relative;
      width: 100%;
      height: 188px;
    }
  }
</style>